// @flow
import React, { Component } from "react";
import TagList from "@/components/TagList.js";
import { TextareaItem, Button, Toast } from "antd-mobile";
import { trim } from "@/utils/utils.js";
// import profession from "@/constant/profession.js";
import { post } from "@/utils/request.js";
import { QUESTION, SUBJECT } from "@/service/api.js";
import { QESTION_TYPE } from "@/utils/constans.js";

class PrePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTag: [],
      tagList: [],
      value: ""
    };
    this.getTagList();
    this.handleChange = this.handleChange.bind(this);
    this.selectedTag = this.selectedTag.bind(this);
  }

  componentDidMount() {
    this.autoFocusInst.focus();
  }
  /**
   * @Description: 
   */
  saveData() {
    const { selectedTag, value } = this.state;
    const userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    post(QUESTION.ADD_QUESTION, {
      subject_ids: selectedTag,
      content: value,
      type: QESTION_TYPE.SHARE,
      user_id: userInfo.id
    }).then(e => {
      if (e.status === 200) {
        Toast.success("~");
        this.props.history.push("/question/questionlist");
      }
    });
  }
  /**
   * @Description: list
   */
  getTagList() {
    post(SUBJECT.GET_ALL_SUBJECT, {
      currentPage: 1,
      pageSize: 30
    }).then(res => {
      let tagList = res.data.rows.map(item => ({
        tagId: item.id,
        tagName: item.title
      }));
      this.setState({
        tagList
      });
    });
  }

  /**
   * @Description: /
   */
  publish(e) {
    const { selectedTag, value } = this.state;
    if (!selectedTag.length) return Toast.info("", 2, null, false);
    if (trim(value) === "") return Toast.info("", 2, null, false);
    console.log("publish");
    this.saveData();
  }

  /**
   * @Description: 
   */
  selectedTag(selectedTag) {
    this.setState({
      selectedTag
    });
  }
  /**
   * @Description: 
   */
  handleChange(value) {
    this.setState({
      value
    });
  }

  render() {
    return (
      <div className="prev">
        <h4 className="prev-title">
          ,,.
        </h4>
        <TagList selectedTag={this.selectedTag} tagList={this.state.tagList} />
        <TextareaItem
          ref={ref => (this.autoFocusInst = ref)}
          className="prev-text"
          placeholder=""
          rows={7}
          count={200}
          value={this.state.value}
          onChange={this.handleChange}
        />
        <div className="prev-btnGroups">
          <Button
            onClick={() => {
              this.props.history.push({
                pathname: "/question/questionlist"
              });
            }}
            inline
            type="primary"
            icon="check-circle-o"
            size="small"
          >
            
          </Button>
          <Button
            onClick={this.publish.bind(this)}
            icon="check-circle-o"
            type="primary"
            inline
            size="small"
          >
            
          </Button>
        </div>
      </div>
    );
  }
}

export default PrePage;



